* {
  padding: 0;
  margin: 0; }

*::after, *::before {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

body {
  background: #333333; }

.fc-w {
  color: #FFFFFF; }

.fc-b {
  color: #1b7baf; }

p, a, li {
  font-family: 'Open Sans', sans-serif; }

img {
  width: 100%; }

/* Sections */
.header {
  background: #1b7baf;
  background: linear-gradient(90deg, rgba(10, 129, 194, 0.500438) 0%, rgba(10, 129, 194, 0.461222) 45%, rgba(0, 213, 255, 0.6) 100%), url("/build/img/header.webp");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 20rem;
  width: 100%; }
  @media (min-width: 992px) {
    .header {
      height: 35rem; } }

.logo .logo-img img {
  width: 90%; }
  @media (min-width: 992px) {
    .logo .logo-img img {
      width: 100%; } }

.nav {
  background: #1b7baf;
  padding: 1rem; }
  @media (min-width: 992px) {
    .nav {
      padding: 1.5rem; } }

.img-nav {
  width: 50%; }
  .img-nav img {
    width: 50%;
    max-width: 20rem; }
  @media (min-width: 992px) {
    .img-nav {
      width: 30%; }
      .img-nav img {
        width: 80%; } }

.nav-items p a {
  color: #FFFFFF;
  text-decoration: none;
  font-weight: bold;
  font-size: 0.8rem;
  margin: auto 1rem;
  border-bottom: 0.2rem solid transparent; }
  @media (min-width: 992px) {
    .nav-items p a {
      font-size: 1.3rem; } }
  .nav-items p a:hover {
    border-bottom: 0.2rem solid #FFFFFF; }

.header-titulo {
  width: 100%; }

.about-me {
  padding: 5rem 1rem; }
  .about-me h2 {
    color: #1b7baf;
    font-weight: bold; }

.about-me-content {
  padding: 2rem 1rem;
  font-size: 1.2rem; }
  .about-me-content span {
    font-weight: bold; }
    .about-me-content span a {
      text-decoration: none;
      color: #229adb; }

.about-icons {
  font-size: 2rem; }
  .about-icons a {
    text-decoration: none; }
  .about-icons .fa-linkedin {
    color: #007bb6; }
  .about-icons .fa-envelope {
    color: #1b7baf; }
  .about-icons .fa-whatsapp-square {
    color: #25D366; }
  .about-icons .fa-telegram-plane {
    color: #0088cc; }
  .about-icons .fa-github-square {
    color: #000000; }
  .about-icons i {
    border-radius: 50%;
    padding: 0.5rem;
    background: #FFFFFF; }
    .about-icons i:hover {
      background: #cccccc;
      border: 0.1rem 0.1rem #1b7baf;
      box-shadow: 0.2rem 0.2rem 0.5rem #000000; }

.about-me-img {
  display: flex;
  justify-content: center;
  align-items: center; }
  .about-me-img p {
    font-weight: 400;
    color: #363636; }
  .about-me-img img {
    width: 100%;
    border-radius: 50%;
    box-shadow: 0.5rem 0.5rem 1.5rem #000000;
    transition: ease-in-out all 0.5s; }
    .about-me-img img:hover {
      transform: scale(0.9);
      box-shadow: 0.8rem 0.8rem 1.5rem #000000; }

